<template>
  <caption>
    <div class="bg">
      <div class="container">
        <img src="../../assets/images/表情包.png">
        <div class="input-bg">
          <input type="text" v-focus placeholder="请输入想要搜索词条的名字或详细信息..">
          <button>搜索词条</button>
        </div>
      </div>
    </div>
  </caption>
</template>
<script>
export default {
  name: 'BodySearch'
}
</script>
<style lang="stylus" scoped>
.bg {
  width: 100%;
  background-image: linear-gradient(white, #aaffcb);

  .container {
    margin: 80px auto;
    width: 600px;

    img {
      user-select: none;
    }

    .input-bg {
      position: relative;

      input[type=text] {
        width: 100%;
        height: 40px;
        padding-left: 15px;
        padding-right: 120px;
        font-size: 18px;
        border: 1px solid black;
        border-radius: 4px;
        box-sizing: border-box;
        outline-color: green;
      }

      button {
        position: absolute;
        top: 0;
        right: 0;
        height: 40px;
        padding: 0 20px;
        font-size: 18px;
        background-color: lightgreen;
        border: 1px solid black;
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        outline: none;
        user-select: none;
        cursor: pointer;

        &:active {
          padding-top: 5px;
        }
      }
    }
  }
}
</style>



